// Component: Search Box

.search-box {
	width: 100%;
	max-width: 700px;
	margin-left: auto;
	margin-right: auto;
	.flex {
		display: flex; }
	.row {
		div.input-with-icon {
			margin-right: 1em;
			margin-bottom: 1em;
			&:last-child {
				margin-right: 0; } } }
	input, select {
		font-family: $font-styled;
		font-size: 1.2em;
		text-align: center; }
	.inp-flex {
		flex: 1; }
	.inp-date {
		width: 32%; }
	.inp-28 {
		width: 28%; } }
