// Component: Button

button {
	background-color: #E6E6E6;
	border: none;
	border-radius: 4px;
	padding: 4px 8px;
	cursor: pointer;
	font-family: $font-styled;
	&.raised {
		padding: 14px 20px;
		background-color: $color-primary;
		box-shadow: $shadow-1;
		border-radius: 10px;
		color: #FFF;
		font-size: 1.1em;
		font-weight: 700;
		transition: box-shadow .15s;
		&:hover {
			box-shadow: $shadow-1-hover; } } }
